* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

@font-face {
  font-family: kemerik-3d;
  src: url("kamerik205-heavy.9d528567.woff2");
  font-weight: 900;
}

@font-face {
  font-family: merriweather-italic-3d;
  src: url("merriweather-regular-italic.ffb80337.woff2");
  font-weight: 900;
}

body {
  color: #fff;
  background-color: #000;
  font-family: kemerik-3d;
}

.logo {
  background-repeat: no-repeat;
  width: 150px;
  height: 150px;
  position: absolute;
}

.layers {
  perspective: 1000px;
}

.layers__container {
  height: 100vh;
  transform-style: preserve-3d;
}

.layers__item {
  background-position: center;
  background-size: cover;
  position: absolute;
  inset: 0;
  transform: rotateX(30deg);
}
